
:root {
    --content-leave-transition-duration: 300ms;
    --content-enter-transition-duration: 500ms;
    --content-enter-transition-starttime: 350ms;
}

@font-face {
    font-family: "m5x7";
    src: url(/about-me/static/media/m5x7.1c36fac5.ttf),
         url(/about-me/static/media/m5x7.b99dd26c.woff),
         url(/about-me/static/media/m5x7.1681d9b9.woff2),
         url(/about-me/static/media/m5x7.dc97bf00.svg#filename);
}

@font-face {
    font-family: "m6x11";
    src: url(/about-me/static/media/m6x11.cc8dfaa3.ttf),
         url(/about-me/static/media/m6x11.06395abf.woff),
         url(/about-me/static/media/m6x11.4b67f4ca.woff2),
         url(/about-me/static/media/m6x11.ec0d4e88.svg#filename);
}

body {
    background-color: black;
    position: absolute;
    height: 99%;
    width: 99%;
}

.logo {
    width: 128px;
    height: 128px;
    image-rendering: pixelated;
    image-rendering: -moz-crisp-edges;
    image-rendering: crisp-edges;
}
  
.header-text {
    font-family: "m6x11";
    font-size: 32px;
    font-weight: normal;
    font-style: normal;

    color: rgb(238, 238, 238);
}

.subheader-text {
    font-family: "m5x7";
    font-size: 32px;
    font-weight: normal;
    font-style: normal;
    
    color: rgb(204, 204, 204);
}

.menu {
    font-family: "m6x11";
    font-size: 16px;
    font-weight: normal;
    font-style: normal;
    
    text-align: center;
    color: white;
}

.menu-item {
}

.content {
    font-family: "m5x7";
    font-size: 24px;
    font-weight: normal;
    font-style: normal;

    padding-left: 16px;
}

